import { Component, OnInit } from '@angular/core';
import { Utils } from '../utils';
import * as $ from 'jquery';
import { AppServiceService } from '../app-service.service';
@Component({
  selector: 'app-case-studies',
  templateUrl: './case-studies.component.html',
  styleUrls: ['./case-studies.component.css']
})
export class CaseStudiesComponent implements OnInit {

  constructor(private appSer: AppServiceService) {
    Utils.loadAllScripts();
  }

  ngOnInit() {

  }

  sendCodes(pathfname, filename) {
    this.appSer.pathfname = pathfname;
    this.appSer.pdfname = filename;
    $('.pilot-head').text('Please share your information');
    $('#labelinputRequirement').text('You are looking for ?');
    $('#inputRequirement').removeAttr('required');
    $('#reqestPilotForm').trigger('reset');
    $('#reqestPilotForm').find('.lable-top-aling').removeClass('lable-top-aling');
    // document.getElementsByClassName('pilot-button')[0].innerText = 'Save and download';
  }
}
