import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AppServiceService {

  constructor(private httpClient: HttpClient) { }
  private options = { headers: new HttpHeaders().set('Content-Type', 'multipart/form-data') };
  pathfname: string = '';
  pdfname: string = '';
  postDataToAPI(params): Observable<any> {
    let baseUrl = `https://host.nexgenitsol.com:9960/WebSite_Pilot.aspx`;
    baseUrl = `${baseUrl}?${params}`;
    return this.httpClient.get(baseUrl).pipe(
      map((res: Response) => {
        return res;
      }),
    );
  }

  postContactInfo(params) {
    let baseUrl = `https://host.nexgenitsol.com:9960/WebSite_Contact.aspx`;
    baseUrl = `${baseUrl}?${params}`;
    return this.httpClient.get(baseUrl).pipe(
      map((res: Response) => {
        return res;
      }),
    );
  }

  postRouteInfo(params) {
    let baseUrl = 'https://host.nexgenitsol.com:9960/website_tracking.aspx';
    baseUrl = `${baseUrl}?${params}`;
    return this.httpClient.get(baseUrl).pipe(
      map((res: Response) => {
        return res;
      }),
    );
  }

  postApplicantInfo(params) {
    //const baseUrl = 'http://localhost:56426/WebSite_Applicants.aspx';
    const baseUrl = 'https://host.nexgenitsol.com:9960/WebSite_Applicants.aspx';
    return this.httpClient.post(baseUrl, params).pipe(
      map((res: Response) => {
        return res;
      })
    );
  }

}

