import { Component, ViewChild, ElementRef, OnInit } from '@angular/core';
import { AppServiceService } from './app-service.service';
import * as $ from 'jquery';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { Utils } from './utils';
import { CookieService } from 'ngx-cookie-service';
declare var require: any;
const FileSaver = require('file-saver');
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {

  showProgressBar: boolean;
  isMobileDevice: boolean;
  isSmallDevice: number;
  showingOptions = [];
  name = 'Angular 5';

  @ViewChild('modalCloseButton', { static: false }) modalCloseButton: ElementRef;

  constructor(private appService: AppServiceService, private cookieService: CookieService,
              private router: Router, private activatedRoute: ActivatedRoute) {

    router.events.subscribe((event) => {
      if (event instanceof NavigationStart) {
        // On navigation starts show loading spinner
        if (document.getElementById('preloader')) {
          document.getElementById('preloader').classList.remove('completed');
        }

        // bind hover event to teach stack drop down
        $('.tech-stack-navigatior,.tech-stack-card').hover(() => {
          $('.tech-stack-card').show();
        }, () => {
          $('.tech-stack-card').hide();
        });
      }

      if (event instanceof NavigationEnd) {
        // On navigation ends get information from coockies and post to api
        const params = Utils.sendUserInfo(this.cookieService, this.router, this.activatedRoute);
        this.appService.postRouteInfo(params).subscribe(res => {
          // Post Api success
        });
        Utils.hideLogo();
      }
    });
  }
  ngOnInit() {
    $('#reqestPilotForm div.input-group label, #reqestPilotForm fieldset label').click(function () {
      $(this).prev().focus();
    });

    this.isSmallDevice = screen.width;

    this.isMobileDevice = navigator.userAgent.indexOf('Mobile') !== -1;
  }
  download_Pdf(filepath, filename) {
    const oReq = new XMLHttpRequest();
    oReq.open('GET', filepath, true);
    oReq.responseType = 'blob';
    // tslint:disable-next-line: only-arrow-functions
    oReq.onload = () => {
      const file = new Blob([oReq.response], {
        type: 'application/pdf'
      });
      FileSaver.saveAs(file, filename);
    };
    oReq.send();
  }
  closeModal(event) {
    setTimeout(() => {
      $('body').css('padding-right', '');
    }, 500);
  }

  openPilotModal() {
    $('#reqestPilotForm').trigger('reset');
    $('#reqestPilotForm').find('.lable-top-aling').removeClass('lable-top-aling');
    $('.pilot-head').text('Request for Pilot');
    $('#labelinputRequirement').text('Requirement Brief');
    $('#inputRequirement').attr('required', 'true');
    setTimeout(() => {
      $('#inputCompanyName').focus();
    }, 200);

  }
  sendPilotInformation(event) {
    this.showProgressBar = true;
    event.preventDefault();
    let guid = this.cookieService.get('uuid');
    if (!guid) {
      guid = '';
    }
    const prarams = `guid=${guid}&companyName=${event.target[0].value}&companyUrl=${event.target[1].value}
    &contactName=${event.target[2].value}&designation=${event.target[3].value}&emailId=${event.target[4].value}
    &directNo=${event.target[5].value}&brief=${event.target[7].value}`;

    this.appService.postDataToAPI(prarams).subscribe(res => {
      this.downloadPdf();
      event.target.reset();
      setTimeout(() => {
        this.modalCloseButton.nativeElement.click();
        this.showProgressBar = false;
      }, 2000);

    }, err => {
      this.downloadPdf();
      $('#reqestPilotForm').find('.lable-top-aling').removeClass('lable-top-aling');
      event.target.reset();
      setTimeout(() => {
        document.getElementById('modalCloseButton').click();
        this.showProgressBar = false;
      }, 2000);
    });
  }

  downloadPdf() {
    const modalHeader: any = document.getElementsByClassName('pilot-head');
    const modalText = modalHeader[0].innerText;
    if (modalText === 'Please share your information') {
      let URLToPDF = '../assets/case-studies/';
      URLToPDF = URLToPDF + this.appService.pathfname;
      this.download_Pdf(URLToPDF, this.appService.pdfname);
    }
  }

  onTechonoloySeleted() {
    if (this.isMobileDevice) {
      $('.menu').removeClass('menu-mobile-active');
    }
  }

  menutoggle() {
    setTimeout(() => {
      if (!$('.menu').hasClass('menu-mobile-active')) {
        $('.menu').addClass('menu-mobile-active');
      }
      // $('.menu.clearfix.menu-mobile-active li.menu-item.has-children').css('display', '');
    }, 200);
  }

  toggleTechStackPop() {
    $('.tech-stack-card').hide();
  }

  onInputValueChange(inputId) {
    if ($(`#${inputId}`).val()) {
      document.getElementById(`label${inputId}`).classList.add('lable-top-aling');
    } else {
      document.getElementById(`label${inputId}`).classList.remove('lable-top-aling');
    }
  }

  toggleArrowIcon(container) {
    const index = this.showingOptions.indexOf(container);
    if (index === -1) {
      this.showingOptions.push(container);
    } else {
      this.showingOptions.splice(index, 1);
    }
  }
}
