import * as $ from 'jquery';
export class Utils {

    public static loadAllScripts() {
        if (document.getElementById('preloader')) {
            document.getElementById('preloader').classList.remove('completed');
        }
        const url = [
            '../assets/vendor/jquery.min.js',
            '../assets/vendor/jquery.countTo.js',
            '../assets/vendor/bootstrap.min.js',
            '../assets/vendor/waypoints.min.js',
            '../assets/vendor/wow.min.js',
            '../assets/vendor/jquery.parallax-1.1.3.js',
            '../assets/vendor/jquery.magnific-popup.min.js',
            '../assets/js/custom.min.js',
            '../assets/js/slider-corporate.js',
            '../assets/js/app.js'
        ];
        $('[defer]').remove();
        url.forEach(item => {
            this.loadScript(item);
        });
        setTimeout(() => {
            this.hideLogo();
        }, 300);
    }

    public static loadScript(url: string) {
        const body = document.body as HTMLDivElement;
        const script = document.createElement('script');
        script.innerHTML = '';
        script.src = url;
        script.async = false;
        script.defer = true;
        body.appendChild(script);
    }

    public static hideLogo() {
        if (document.getElementById('preloader')) {
            document.getElementById('preloader').classList.add('completed');
        }
    }

    public static sendUserInfo(cookieService, routerInfo, activatedRoute) {
        let coockieUUId = null;
        let coockieBrowseType = null;
        let routingUrl = routerInfo.url;
        if (routingUrl === '/') {
            routingUrl = '/home';
        }
        if (activatedRoute.snapshot.queryParams.guid) {
            coockieUUId = activatedRoute.snapshot.queryParams.guid;
            coockieBrowseType = 'Email';
            cookieService.set('uuid', coockieUUId);
            cookieService.set('BrowseType', 'Email');
        } else {
            if (cookieService.get('uuid')) {
                coockieUUId = cookieService.get('uuid');
                coockieBrowseType = cookieService.get('BrowseType');
            } else {
                cookieService.set('uuid', this.generateUUId());
                cookieService.set('BrowseType', 'Web');
                coockieUUId = cookieService.get('uuid');
                coockieBrowseType = cookieService.get('BrowseType');
            }
        }
        if (routingUrl.includes('?')) {
            routingUrl = routingUrl.split('?')[0];
            if (routingUrl === '/') {
                routingUrl = '/home';
            }
        }
        return `guid=${coockieUUId}&requestUrl=${routingUrl}&source=${coockieBrowseType}`;
    }

    public static generateUUId() {
        return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
            let r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
            return v.toString(16);
        });
    }

    public static openRequestPop() {
        $('#nexCantactUs').trigger('click');
        $('.pilot-head').text('Request Specialist');
    }

}
