import { Component, OnInit, ViewEncapsulation, AfterViewInit } from '@angular/core';
import { Utils } from '../utils';
import * as $ from 'jquery';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  encapsulation: ViewEncapsulation.None
})

export class HomeComponent implements OnInit, AfterViewInit {

  constructor() {
    Utils.loadAllScripts();
  }

  isMobileDevice: boolean;
  deviceHeight: any;
  deviceWidth: any;
  carasoulTitleTextLeft = '';
  carsoulTitleTextTop = '';

  ngOnInit() {

    const rightIconLeft = $('.carousel-control-next-icon').offset();
    if (screen.width > 600 && screen.width < 800) {
      const textLeft = rightIconLeft.left - $('#title-content').width();
      this.carasoulTitleTextLeft = `${textLeft / 2 + 25}px`;
      this.carsoulTitleTextTop = `${rightIconLeft.top - 75}px`;
    }

    window.addEventListener('scroll', (e) => {
      $('.slotholder').css('transform', '');
    });
    this.isMobileDevice = navigator.userAgent.indexOf('Mobile') !== -1;
    this.deviceHeight = $('.carousel-inner').height();
    this.deviceHeight = `${this.deviceHeight}px`;
    this.deviceWidth = $('.carousel-inner').width();
    this.deviceWidth = this.deviceWidth * 2;
    this.deviceWidth = `${this.deviceWidth}px`;
  }

  closeModal(modalId) {
    document.getElementById(modalId).classList.remove('in');
    document.getElementById(modalId).classList.remove('show');
  }

  getActiveDiv(slideInfo) {
    return $(`.${slideInfo}`).hasClass('active');
  }

  ngAfterViewInit() {
    Utils.hideLogo();
  }

}
