import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Utils } from '../utils';
import * as $ from 'jquery';
import { AppServiceService } from '../app-service.service';
import { HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-careers',
  templateUrl: './careers.component.html',
  styleUrls: ['./careers.component.css']
})
export class CareersComponent implements OnInit {

  showProgressBar = false;
  resumeFile: any;
  applicantRole = '';
  appliedJobRole = '';
  @ViewChild('applicantModalCloseButton', { static: false }) applicantModalCloseButton: ElementRef;
  constructor(private appService: AppServiceService) {
    Utils.loadAllScripts();
  }

  ngOnInit() {
    $('#jobApplicationForm div.input-group label, #jobApplicationForm fieldset label').click(function () {
      $(this).prev().focus();
    });
  }

  onInputValueChange(inputId) {
    if ($(`#${inputId}`).val()) {
      document.getElementById(`label${inputId}`).classList.add('lable-top-aling');
    } else {
      document.getElementById(`label${inputId}`).classList.remove('lable-top-aling');
    }
  }

  uploadResume(file) {
    this.resumeFile = file.target.files[0];
    const fileName = file.target.files[0].name;
    document.getElementById('resumeUploadLbl').innerText = fileName;
  }

  getApplicantType(event, action) {
    this.applicantRole = action;
  }

  getJobDescription(jobType) {
    document.getElementById('resumeUploadLbl').innerText = 'Upload Resume';
    this.appliedJobRole = jobType;
    $('#jobApplicationForm').trigger('reset');
    $('#jobApplicationForm').find('.lable-top-aling').removeClass('lable-top-aling');
    setTimeout(() => {
      $('#inputCompanyName').focus();
    }, 200);
  }

  sendPilotInformation(event) {
    event.preventDefault();
    this.showProgressBar = true;
    const formData = new FormData();
    formData.append('file', this.resumeFile);
    formData.append('applicantName', event.target[0].value);
    formData.append('applicantEmail', event.target[3].value);
    formData.append('applicantPhno', event.target[4].value);
    formData.append('applicantDesc', event.target[6].value);
    formData.append('applicantRole', `${this.applicantRole}  ${this.appliedJobRole}`);

    this.appService.postApplicantInfo(formData).subscribe(res => {
      this.showProgressBar = false;
      document.getElementById('applicantModalCloseButton').click();
      console.log(JSON.stringify(res));
    }, err => {
      this.showProgressBar = false;
      document.getElementById('applicantModalCloseButton').click();
    });
  }
}
